@media
only screen and (-webkit-min-device-pixel-ratio: 2),
only screen and (   min--moz-device-pixel-ratio: 2),
only screen and (     -o-min-device-pixel-ratio: 2/1),
only screen and (        min-device-pixel-ratio: 2),
only screen and (                min-resolution: 192dpi),
only screen and (                min-resolution: 2dppx) {

    #logo a.standard-logo,
    #header.split-menu #logo a.standard-logo { display: none; }

    #logo a.retina-logo { display: block; }

    #header.split-menu #logo a.retina-logo { display: inline-block; }

    .owl-carousel.owl-loading,
    .preloader,
    .preloader2,
    .form-process {
        background-image: url('../images/canvas/preloader@2x.gif');
        background-size: 24px 24px;
    }

    .dark .owl-carousel.owl-loading,
    .dark .preloader,
    .dark .preloader2,
    .dark .form-process { background-image: url('../images/canvas/preloader-dark@2x.gif'); }

    .widget_recent_comments li {
        background-image: url("../images/canvas/icons/widget-comment@2x.png");
        background-size: 15px 26px;
    }

    .widget_nav_menu li a,
    .widget_links li a,
    .widget_meta li a,
    .widget_archive li a,
    .widget_recent_entries li a,
    .widget_categories li a,
    .widget_pages li a,
    .widget_rss li a,
    .widget_recent_comments li a {
        background-image: url("../images/canvas/icons/widget-link@2x.png");
        background-size: 4px 20px;
    }

    .dark .widget_recent_comments li { background-image: url("../images/canvas/icons/widget-comment-dark@2x.png"); }

    .dark .widget_nav_menu li a,
    .dark .widget_links li a,
    .dark .widget_meta li a,
    .dark .widget_archive li a,
    .dark .widget_recent_entries li a,
    .dark .widget_categories li a,
    .dark .widget_pages li a,
    .dark .widget_rss li a,
    .dark .widget_recent_comments li a { background-image: url("../images/canvas/icons/widget-link-dark@2x.png"); }

}

@media
only screen and (-webkit-min-device-pixel-ratio: 2) and (max-width: 991.98px),
only screen and (   min--moz-device-pixel-ratio: 2) and (max-width: 991.98px),
only screen and (     -o-min-device-pixel-ratio: 2/1) and (max-width: 991.98px),
only screen and (        min-device-pixel-ratio: 2) and (max-width: 991.98px),
only screen and (                min-resolution: 192dpi) and (max-width: 991.98px),
only screen and (                min-resolution: 2dppx) and (max-width: 991.98px) {
    #logo a.standard-logo { display: none; }
    #logo a.retina-logo { display: inline-block; }
}

